import { animate, state, style, transition, trigger } from '@angular/animations';
import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { LegacyPageEvent as PageEvent } from '@angular/material/legacy-paginator';
import { ChipOptionModel, ChipType } from '@tei/material';
import { concat, Observable, of, Subject } from 'rxjs';
import { catchError, debounceTime, delay, switchMap } from 'rxjs/operators';
import { ICategoryService } from '../../@intermediate/category.interface';
import { IGlossaryService } from '../../@intermediate/glossary.interface';
import { ILanguageService } from '../../@intermediate/language.interface';
import { GetCategoriesRequest, GetCategoriesResponse } from '../../@intermediate/models/getCategories.model';
import { GetGlossariesRequest, GetGlossariesResponse, GlossaryItem } from '../../@intermediate/models/getGlossaries.model';
import { GetLanguagesRequest, GetLanguagesResponse, Language } from '../../@intermediate/models/getLanguages.model';
import { PutGlossaryRequest } from '../../@intermediate/models/putGlossary.model';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-admin-translations-glossary',
  templateUrl: './glossary.component.html',
  styleUrls: ['./glossary.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class GlossaryComponent implements OnInit
{
  ChipType = ChipType;
  chipItemsWithDisplayName = [
    { chipName: 'Name', displayName: this.translate.instant('TRANSLATIONS.MODULE') },
  ];
  chipItems: string[] = ['CategoryName'];
  contactTypeaheadInput$ = new Array();
  quickSearchValue = {};
  items: Observable<any[]>;
  contactArray: Observable<any[]> = of([]);
  sourceText = '';
  targetText = '';

  tableColumns: string[] = ["Module", "Source", "Target", "Status", "MobileMode"];
  expandedElement: GlossaryItem;
  dataSource: any;

  allCategories: GetCategoriesResponse;
  allLanguages: GetLanguagesResponse;
  glossariesData: GetGlossariesResponse;

  pageLength = 100;
  pageSize = 25;
  pageIndex = 1;
  pageSizeOptions: number[] = [5, 10, 25, 100];
  pageEvent: PageEvent;

  selectedSourceLanguageID: number;
  selectedTargetLanguageID: number;
  getGlossariesRequest: GetGlossariesRequest = new GetGlossariesRequest();
  glossaryItemValue: string;
  valuechangedId: number;
  @Input() verifiedStatus: boolean = false;
  @Input() selectedLanguages: { sourceId: number; targetId: number } = { sourceId: null, targetId: null };
  @Output() changeLanguages = new EventEmitter<{ sourceId: number; targetId: number }>();

  constructor(
    @Inject('IGlossaryService') private glossaryService: IGlossaryService,
    @Inject('ILanguageService') private languageService: ILanguageService,
    @Inject('ICategoryService') private categoryService: ICategoryService,
    private translate: TranslateService

  ) { }
  setPageSizeOptions(setPageSizeOptionsInput: string)
  {
    if (setPageSizeOptionsInput)
    {
      this.pageSizeOptions = setPageSizeOptionsInput.split(',').map(str => +str);
    }
  }
  ngOnInit(): void
  {
    this.chipItems.forEach(el =>
    {
      this.contactTypeaheadInput$.push(new Subject<string>());
    });

    this.load();
  }

  focusOutFunction(event: any, glossaryOriginalText: string)
  {
    setTimeout(() =>
    {

      this.valuechangedId = 0;
      event.target.value = glossaryOriginalText;
    }, 200);
  }

  valuechange(glossaryNeText: string, glossaryItem: GlossaryItem)
  {
    this.glossaryItemValue = glossaryNeText;
    this.valuechangedId = glossaryItem.GlossaryItemID;
  }

  load()
  {
    let getLanguagesrequest = new GetLanguagesRequest();
    this.languageService.Get(getLanguagesrequest).subscribe((languages: GetLanguagesResponse) =>
    {
      this.allLanguages = languages;
      if(this.selectedLanguages.sourceId && this.selectedLanguages.targetId) 
      {
        this.selectedSourceLanguageID = this.selectedLanguages.sourceId;
        this.selectedTargetLanguageID = this.selectedLanguages.targetId;
      }else {
        this.selectedSourceLanguageID = this.allLanguages.Languages[0].LanguageID;
        this.selectedTargetLanguageID = this.allLanguages.TargetLanguages[0].LanguageID;
        this.changeLanguages.emit({ sourceId: this.selectedSourceLanguageID, targetId: this.selectedTargetLanguageID })
      }

      this.loadGlossaries();
    });

    let getCategoriesRequest = new GetCategoriesRequest();
    this.categoryService.Get(getCategoriesRequest).subscribe((categories: GetCategoriesResponse) =>
    {
      this.allCategories = categories;
    });
  }

  loadGlossaries(request?: GetGlossariesRequest)
  {
    if (!request)
    {
      this.getGlossariesRequest.PageNo = this.pageIndex;
      this.getGlossariesRequest.PageSize = this.pageSize;
      this.getGlossariesRequest.AllItemApproved = this.verifiedStatus;
      this.getGlossariesRequest.SourceLanguageID = this.selectedSourceLanguageID;
      this.getGlossariesRequest.TargetLanguageID = this.selectedTargetLanguageID;
    } else
    {
      this.getGlossariesRequest = request;
    }

    this.getGlossariesRequest.SourceText = this.sourceText || '';
    this.getGlossariesRequest.TargetText = this.targetText || '';

    this.glossaryService.Get(this.getGlossariesRequest).subscribe((glossaryData: GetGlossariesResponse) =>
    {
      this.glossariesData = glossaryData;
      this.pageLength = glossaryData?.TotalRecords || 0;
    });
  }

  verifyUnverifyItem(glossary: GlossaryItem)
  {
    let request = new PutGlossaryRequest();
    request = {
      GlossaryItemID: glossary.GlossaryItemID,
      IsApproved: !glossary.IsApproved,
      ... !glossary.IsApproved && {Translation: glossary.Value}
    };
    this.glossaryService.Put(request).subscribe(res =>
    {
      this.loadGlossaries();
    });
  }

  filterByChipItem(item: { chipName: string, index: number; })
  {
    this.contactArray[item.index] = concat(
      of([...this.allCategories?.Categories]), // default items
      this.contactTypeaheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm = "") =>
          this.getResult(searchTerm as string).pipe(
            catchError(() => of([])),
          ))

      )
    );
  }

  getResult(term: string = ""): Observable<any[]>
  {
    let items = [...this.allCategories?.Categories];
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x.Name.toLocaleLowerCase().indexOf(lowerTerm) > -1
      );
    }
    return of(items);
  }

  onChangeSourcheLanguage(languageId: number)
  {
    this.selectedSourceLanguageID = languageId;
    this.changeLanguages.emit({ sourceId: this.selectedSourceLanguageID, targetId: this.selectedTargetLanguageID })
    this.loadGlossaries();
  }

  onChangeTargetLanguage(languageId: number)
  {
    this.selectedTargetLanguageID = languageId;
    this.changeLanguages.emit({ sourceId: this.selectedSourceLanguageID, targetId: this.selectedTargetLanguageID })
    this.loadGlossaries();
  }

  onChangePaginator(pageEvent: PageEvent)
  {
    this.pageSize = pageEvent?.pageSize;
    this.pageIndex = pageEvent?.pageIndex + 1;
    this.loadGlossaries();
  }

  onFilterChange(filterObj: any)
  {
    let request = new GetGlossariesRequest();
    request = {
      PageSize: this.pageSize,
      PageNo: this.pageIndex,
      AllItemApproved: this.verifiedStatus,
      SourceLanguageID: this.selectedSourceLanguageID,
      TargetLanguageID: this.selectedTargetLanguageID
    };

    let obj: GetGlossariesRequest;
    filterObj.forEach(filterItem =>
    {
      if (filterItem.value)
      {
        this.allCategories.Categories.find((res) =>
        {
          if (res.Name === filterItem.value)
          {
            obj = {
              CategoryID: res.CategoryID
            };
            Object.assign(request, obj);
          }
        });
      }
    });
    this.loadGlossaries(request);
  }

  onSourceChange(chipOptionModel: ChipOptionModel): void
  {
    this.sourceText = chipOptionModel.firstValue;
    this.loadGlossaries();
  }

  onTargetFilterChange(chipOptionModel: ChipOptionModel): void
  {
    this.targetText = chipOptionModel.firstValue;
    this.loadGlossaries();
  }
}
