export class GetGlossariesRequest
{
  SourceLanguageID?: number;
  TargetLanguageID?: number;
  CategoryID?: number;
  SourceText?: string;
  TargetText?: string;
  PageNo?: number;
  PageSize?: number;
  SortColumn?: string;
  SortOrder?: string;
  AllItemApproved?: boolean;
}



export class Glossary
{
  CategoryName: string;
  GlossaryID: number;
  SourceText: string;
  SourceLanguage: string;
  Culture: string;
  CultureFlagUrl: string;
  NotApprovedCount: number;
  GlossaryItems: GlossaryItem[];
}

export class GlossaryItem
{
  GlossaryItemID: number;
  Value: string;
  Culture: string;
  ModifiedBy: string;
  DateModified: Date;
  IsApproved: boolean;
}

export class GetGlossariesResponse
{
  Glossaries: Glossary[];
  TotalRecords: number;
}
