import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCategoriesRequest, GetCategoriesResponse } from '../@intermediate/models/getCategories.model';

@Injectable({
  providedIn: 'root'
})
export class CategoryService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {

    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/Translations';

  }

  public Get(request: GetCategoriesRequest): Observable<GetCategoriesResponse>
  {
    const apiUrl = this.serviceUrl + '/Glossary/GetCategories';
    return this.http.get<GetCategoriesResponse>(apiUrl, { params: request as any });
  }

}
