import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetLanguagesRequest, GetLanguagesResponse } from '../@intermediate/models/getLanguages.model';

@Injectable({
  providedIn: 'root'
})
export class LanguageService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {

    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/Translations';

  }

  public Get(request: GetLanguagesRequest): Observable<GetLanguagesResponse>
  {
    const apiUrl = this.serviceUrl + '/Glossary/GetLanguages';
    return this.http.get<GetLanguagesResponse>(apiUrl, { params: request as any });
  }
}
