import { animate, state, style, transition, trigger } from '@angular/animations';
import { Component } from '@angular/core';
import { BreadCrumb } from '@tei/material';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-admin-translations-translator',
  templateUrl: './translator.component.html',
  styleUrls: ['./translator.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})

export class TranslatorComponent
{
  pageLength = 100;
  pageSize = 25;
  pageIndex = 1;
  pageSizeOptions: number[] = [5, 10, 25, 100];
  verifiedStatus: boolean = false;
  selectedLanguages: {sourceId: number, targetId: number} = {sourceId: null, targetId: null};
  
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TRANSLATIONS.PORTAL_ADMIN_CENTER'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('TRANSLATIONS.TRANSLATIONS'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('TRANSLATIONS.AUTOMATED_TRANSLATIONS'),
      relativeUrl: ''
    }
  ];

  constructor(
    private translate: TranslateService
  ) { }

  changeTab(event: number)
  {
    this.verifiedStatus = event === 0 ? false : true;
  }
  
  changeLanguages(event: {sourceId: number, targetId: number}): void
  {
    this.selectedLanguages = event;
  }
}

