import { Component } from '@angular/core';
import { CategoryEnum, EventTypeEnum, GenericEventModel, GenericEventService, RouteService } from '@tei/core';

@Component({
  selector: 'tei-admin-translations',
  templateUrl: './translations.component.html',
  styleUrls: ['./translations.component.scss']
})
export class TranslationsComponent
{
  constructor(private readonly routeService: RouteService, private genericEventService: GenericEventService)
  {
    this.genericEventService.onChangeGenericEvent({
      module: { Category: CategoryEnum.Pos, Name: 'admin-translations' },
      eventType: EventTypeEnum.onChangeSideBarVisibility,
      data: { sideBarVisible: true, headerTitle: 'Payment' }
    } as GenericEventModel);

    this.routeService.ngOnInit();
  }
}
