import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetGlossariesRequest, GetGlossariesResponse } from '../@intermediate/models/getGlossaries.model';
import { PutGlossaryRequest, PutGlossaryResponse } from '../@intermediate/models/putGlossary.model';

@Injectable({
  providedIn: 'root'
})

export class GlossaryDataService
{

  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Admin/Translations';
  }


  public Get(request: GetGlossariesRequest): Observable<GetGlossariesResponse>
  {
    const apiUrl = this.serviceUrl + '/Glossary';
    return this.http.get<GetGlossariesResponse>(apiUrl, { params: request as any });
  }


  public Put(model: PutGlossaryRequest): Observable<PutGlossaryResponse>
  {
    const apiUrl = this.serviceUrl + '/Glossary';
    return this.http.put<PutGlossaryResponse>(apiUrl, model);
  }

}
